import {
	ArrowLeftIcon,
	InformationCircleIcon,
	PlusIcon,
} from "@heroicons/react/24/solid"
import {Alert, Badge, Button, Group, Modal, Select, Text} from "@mantine/core"
import {useDisclosure} from "@mantine/hooks"
import type {DataFunctionArgs, SerializeFrom} from "@remix-run/node"
import {json} from "@remix-run/node"
import {Link, useFetcher, useLoaderData} from "@remix-run/react"
import * as React from "react"
import {toast} from "sonner"
import invariant from "tiny-invariant"
import {z} from "zod"
import {TailwindContainer} from "~/components/TailwindContainer"
import {getAllEvents} from "~/lib/event.server"
import {
	createReservation,
	getReservationsByStudentId,
} from "~/lib/reservation.server"
import {requireUserId} from "~/session.server"
import {formatDateTime} from "~/utils/misc"
import {badRequest} from "~/utils/misc.server"
import {validateAction, type inferErrors} from "~/utils/validation"

const CreateReservationSchema = z.object({
	eventId: z.string().min(1, "EventId is required"),
})

interface ActionData {
	success: boolean
	message: string
	fieldErrors?: inferErrors<typeof CreateReservationSchema>
}

export const loader = async ({request}: DataFunctionArgs) => {
	const studentId = await requireUserId(request)

	const eventsResponse = await getAllEvents()
	const reservationsResponse = await getReservationsByStudentId(studentId)

	if (eventsResponse.success && reservationsResponse.success) {
		const events = eventsResponse.data
		const reservations = reservationsResponse.data

		invariant(events && reservations, "Events and reservations must be defined")

		return json({
			events,
			reservations,
		})
	}

	return json({
		events: [],
		reservations: [],
	})
}

export const action = async ({request}: DataFunctionArgs) => {
	const studentId = await requireUserId(request)
	const {fields, fieldErrors} = await validateAction(
		request,
		CreateReservationSchema
	)

	if (fieldErrors) {
		return badRequest<ActionData>({
			success: false,
			message: "Invalid fields",
			fieldErrors,
		})
	}

	const response = await createReservation({
		studentId,
		eventId: fields.eventId,
	})

	if (!response.success) {
		return badRequest<ActionData>({
			success: false,
			message: response.message,
			fieldErrors: {
				eventId: response.message,
			},
		})
	}

	return json<ActionData>({
		success: true,
		message: response.message,
	})
}

export default function Events() {
	const {reservations, events} = useLoaderData<typeof loader>()

	const fetcher = useFetcher<ActionData>()
	const [isModalOpen, handleModal] = useDisclosure(false)

	const isSubmitting = fetcher.state !== "idle"

	React.useEffect(() => {
		if (fetcher.state !== "idle") {
			return
		}

		if (!fetcher.data) return

		if (fetcher.data.success) {
			toast.success(fetcher.data.message)
			handleModal.close()
		} else {
			toast.error(fetcher.data.message)
		}

		// handleModal is not meemoized, so we don't need to add it to the dependency array
		// eslint-disable-next-line react-hooks/exhaustive-deps
	}, [fetcher.data, fetcher.state])

	return (
		<>
			<TailwindContainer className="rounded-md bg-white">
				<div className="mt-8 px-4 py-10 sm:px-6 lg:px-8">
					<div className="sm:flex sm:flex-auto sm:items-center sm:justify-between">
						<div>
							<Button
								leftIcon={<ArrowLeftIcon className="h-4 w-4" />}
								variant="white"
								size="md"
								component={Link}
								to=".."
								pl={0}
								mb={20}
								color="gray"
							>
								Back
							</Button>
							<h1 className="text-3xl font-semibold text-gray-900">
								My Events
							</h1>
							<p className="mt-2 text-sm text-gray-700">
								Here you can see all the events you have reserved tickets for.
							</p>
						</div>
						<div>
							<Button loaderPosition="left" onClick={() => handleModal.open()}>
								<PlusIcon className="h-4 w-4" />
								<span className="ml-2">Reserve Tickets</span>
							</Button>
						</div>
					</div>
					<div className="mt-8 flex flex-col">
						<div className="-mx-4 -my-2 overflow-x-auto sm:-mx-6 lg:-mx-8">
							<div className="inline-block min-w-full py-2 align-middle md:px-6 lg:px-8">
								<table className="min-w-full divide-y divide-gray-300">
									<thead>
										<tr>
											<th
												scope="col"
												className="py-3.5 pl-4 pr-3 text-left text-sm font-semibold text-gray-900 sm:pl-6 md:pl-0"
											>
												Event
											</th>

											<th
												scope="col"
												className="py-3.5 pl-4 pr-3 text-left text-sm font-semibold text-gray-900 sm:pl-6 md:pl-0"
											>
												Schedule
											</th>

											<th
												scope="col"
												className="py-3.5 pl-4 pr-3 text-left text-sm font-semibold text-gray-900 sm:pl-6 md:pl-0"
											>
												Seat Number
											</th>

											<th
												scope="col"
												className="hidden px-3 py-3.5 text-left text-sm font-semibold text-gray-900 sm:table-cell"
											>
												Event Status
											</th>
											<th
												scope="col"
												className="relative py-3.5 pl-3 pr-4 sm:pr-6 md:pr-0"
											></th>
										</tr>
									</thead>
									<tbody className="divide-y divide-gray-200">
										{reservations.map((reservation) => (
											<ReservationRow
												key={reservation.id}
												reservations={reservation}
											/>
										))}
									</tbody>
								</table>
							</div>
						</div>
					</div>
				</div>
			</TailwindContainer>

			<Modal
				opened={isModalOpen}
				onClose={() => handleModal.close()}
				title="Buy tickets"
				overlayProps={{
					opacity: 0.6,
					blur: 1.2,
				}}
			>
				<fetcher.Form method="post" replace>
					<fieldset disabled={isSubmitting} className="flex flex-col gap-4">
						{fetcher.data?.fieldErrors?.eventId && (
							<Alert
								icon={<InformationCircleIcon className="h-6 w-6" />}
								title="Error"
								color="red"
							>
								{fetcher.data.fieldErrors.eventId}
							</Alert>
						)}

						<Select
							name="eventId"
							label="Event"
							withinPortal
							placeholder="Select an event"
							nothingFound="No events found"
							itemComponent={SelectItem}
							data={events.map((event) => {
								const availableSeats = event.capacity - event.reservedSeats

								return {
									start: event.startDateTime,
									end: event.endDateTime,
									address: event.stadium.name,
									label: `${event.name} (${availableSeats} seats available)`,
									value: event.id,
									disabled: availableSeats === 0,
								}
							})}
							error={Boolean(fetcher.data?.fieldErrors?.eventId)}
							required
						/>

						<div className="mt-1 flex items-center justify-end gap-4">
							<Button
								variant="subtle"
								disabled={isSubmitting}
								onClick={() => handleModal.close()}
								color="red"
							>
								Cancel
							</Button>
							<Button
								type="submit"
								loading={isSubmitting}
								loaderPosition="right"
							>
								Reserve your seat
							</Button>
						</div>
					</fieldset>
				</fetcher.Form>
			</Modal>
		</>
	)
}

function ReservationRow({
	reservations,
}: {
	reservations: SerializeFrom<typeof loader>["reservations"][number]
}) {
	return (
		<tr key={reservations.id}>
			<td className="whitespace-nowrap py-4 pl-4 pr-3 text-sm font-medium text-gray-900 sm:pl-6 md:pl-0">
				{reservations.event.name}
			</td>

			<td className="whitespace-nowrap py-4 pl-4 pr-3 text-sm font-medium text-gray-900 sm:pl-6 md:pl-0">
				<p>{formatDateTime(reservations.event.startDateTime)}</p>
				<p>{formatDateTime(reservations.event.endDateTime)}</p>
			</td>

			<td className="whitespace-nowrap px-3 py-4 text-sm text-gray-500">
				{reservations.seatNumber}
			</td>

			<td className="whitespace-nowrap px-3 py-4 text-sm text-gray-500">
				<Badge color={reservations.isCancelled ? "red" : "green"}>
					{reservations.isCancelled ? "Cancelled" : "Active"}
				</Badge>
			</td>

			<td className="relative space-x-4 whitespace-nowrap py-4 pl-3 pr-4 text-left text-sm font-medium sm:pr-6 md:pr-0"></td>
		</tr>
	)
}

interface ItemProps extends React.ComponentPropsWithoutRef<"div"> {
	start: string
	end: string
	address: string
	label: string
}

const SelectItem = React.forwardRef<HTMLDivElement, ItemProps>(
	(props: ItemProps, ref) => {
		const {start, end, address, label, ...others} = props
		return (
			<div ref={ref} {...others}>
				<Group noWrap>
					<div>
						<Text size="sm">{label}</Text>
						<Text size="xs" opacity={0.65}>
							{address}
						</Text>
						<Text size="xs" opacity={0.65}>
							{formatDateTime(start)} - {formatDateTime(end)})
						</Text>
					</div>
				</Group>
			</div>
		)
	}
)
