import {ArrowLeftIcon, PlusIcon} from "@heroicons/react/24/solid"
import {Button, Modal, TextInput, Textarea, clsx} from "@mantine/core"
import {useDisclosure} from "@mantine/hooks"
import type {ActionFunction} from "@remix-run/node"
import {json} from "@remix-run/node"
import {Link, useFetcher, useLoaderData} from "@remix-run/react"
import * as React from "react"
import {z} from "zod"
import {TailwindContainer} from "~/components/TailwindContainer"
import {createSport, getAllSports, updateSport} from "~/lib/sport.server"
import {badRequest} from "~/utils/misc.server"
import type {inferErrors} from "~/utils/validation"
import {validateAction} from "~/utils/validation"

enum MODE {
	edit,
	add,
}

const ManageSportSchema = z.object({
	sportId: z.string().optional(),
	name: z.string().min(1, "Name is required"),
	description: z.string().min(1, "Description is required"),
})

interface ActionData {
	success: boolean
	fieldErrors?: inferErrors<typeof ManageSportSchema>
}

export const loader = async () => {
	const sportsResponse = await getAllSports()

	if (!sportsResponse.success) {
		return json({sports: []})
	}

	const sports = sportsResponse.data!
	return json({sports})
}

export const action: ActionFunction = async ({request}) => {
	const {fields, fieldErrors} = await validateAction(request, ManageSportSchema)

	if (fieldErrors) {
		return badRequest<ActionData>({success: false, fieldErrors})
	}

	const {sportId, ...rest} = fields
	if (sportId) {
		await updateSport(sportId, rest)
		return json({success: true})
	}

	await createSport({...rest})

	return json({success: true})
}

export default function ManageStadium() {
	const fetcher = useFetcher<ActionData>()
	const {sports} = useLoaderData<typeof loader>()

	type _Sport = (typeof sports)[number]

	const [selectedSportId, setSelectedSportId] = React.useState<
		_Sport["id"] | null
	>(null)
	const [selectedSport, setSelectedSport] = React.useState<_Sport | null>(null)
	const [mode, setMode] = React.useState<MODE>(MODE.edit)
	const [isModalOpen, handleModal] = useDisclosure(false)

	const isSubmitting = fetcher.state !== "idle"

	React.useEffect(() => {
		if (fetcher.state !== "idle" && fetcher.submission === undefined) {
			return
		}

		if (fetcher.data?.success) {
			setSelectedSportId(null)
			handleModal.close()
		}
		// handleModal is not meemoized, so we don't need to add it to the dependency array
		// eslint-disable-next-line react-hooks/exhaustive-deps
	}, [fetcher.data?.success, fetcher.state, fetcher.submission])

	React.useEffect(() => {
		if (!selectedSportId) {
			setSelectedSport(null)
			return
		}

		const sport = sports.find((sport) => sport.id === selectedSportId)
		if (!sport) return

		setSelectedSport(sport)
		handleModal.open()
		// handleModal is not meemoized, so we don't need to add it to the dependency array
		// eslint-disable-next-line react-hooks/exhaustive-deps
	}, [sports, selectedSportId])

	return (
		<>
			<TailwindContainer className="rounded-md bg-white">
				<div className="mt-8 px-4 py-10 sm:px-6 lg:px-8">
					<div className="sm:flex sm:flex-auto sm:items-center sm:justify-between">
						<div>
							<Button
								leftIcon={<ArrowLeftIcon className="h-4 w-4" />}
								variant="white"
								size="md"
								component={Link}
								to=".."
								pl={0}
								mb={20}
								color="gray"
							>
								Back
							</Button>
							<h1 className="text-3xl font-semibold text-gray-900">
								Manage Sports
							</h1>
							<p className="mt-2 text-sm text-gray-700">
								Manage the sports that are available for selection when an
								organizer creates an event.
							</p>
						</div>
						<div>
							<Button
								loading={isSubmitting}
								loaderPosition="left"
								onClick={() => {
									setMode(MODE.add)
									handleModal.open()
								}}
							>
								<PlusIcon className="h-4 w-4" />
								<span className="ml-2">Add Sport</span>
							</Button>
						</div>
					</div>
					<div className="mt-8 flex flex-col">
						<div className="-mx-4 -my-2 overflow-x-auto sm:-mx-6 lg:-mx-8">
							<div className="inline-block min-w-full py-2 align-middle md:px-6 lg:px-8">
								<table className="min-w-full divide-y divide-gray-300">
									<thead>
										<tr>
											<th
												scope="col"
												className="py-3.5 pl-4 pr-3 text-left text-sm font-semibold text-gray-900 sm:pl-6 md:pl-0"
											>
												Name
											</th>

											<th
												scope="col"
												className="py-3.5 pl-4 pr-3 text-left text-sm font-semibold text-gray-900 sm:pl-6 md:pl-0"
											>
												Description
											</th>

											<th
												scope="col"
												className="relative py-3.5 pl-3 pr-4 sm:pr-6 md:pr-0"
											></th>
										</tr>
									</thead>
									<tbody className="divide-y divide-gray-200">
										{sports.map((sport) => (
											<tr key={sport.id}>
												<td className="whitespace-nowrap py-4 pl-4 pr-3 text-sm font-medium text-gray-900 sm:pl-6 md:pl-0">
													{sport.name}
												</td>
												<td className="whitespace-nowrap py-4 pl-4 pr-3 text-sm font-medium text-gray-900 sm:pl-6 md:pl-0">
													{sport.description}
												</td>
												<td className="relative space-x-4 whitespace-nowrap py-4 pl-3 pr-4 text-left text-sm font-medium sm:pr-6 md:pr-0">
													<div className="flex items-center gap-6">
														<Button
															loading={isSubmitting}
															variant="subtle"
															loaderPosition="right"
															onClick={() => {
																setSelectedSportId(sport.id)
																setMode(MODE.edit)
															}}
														>
															Edit
														</Button>
													</div>
												</td>
											</tr>
										))}
									</tbody>
								</table>
							</div>
						</div>
					</div>
				</div>
			</TailwindContainer>

			<Modal
				opened={isModalOpen}
				onClose={() => {
					setSelectedSportId(null)
					handleModal.close()
				}}
				title={clsx({
					"Edit sport": mode === MODE.edit,
					"Add sport": mode === MODE.add,
				})}
				centered
				overlayProps={{blur: 1.2, opacity: 0.6}}
			>
				<fetcher.Form method="post" replace>
					<fieldset disabled={isSubmitting} className="flex flex-col gap-4">
						<input type="hidden" name="sportId" value={selectedSport?.id} />

						<TextInput
							name="name"
							label="Name"
							defaultValue={selectedSport?.name}
							error={fetcher.data?.fieldErrors?.name}
							required
						/>

						<Textarea
							name="description"
							label="Description"
							defaultValue={selectedSport?.name}
							error={fetcher.data?.fieldErrors?.name}
							rows={4}
							required
						/>

						<div className="mt-1 flex items-center justify-end gap-4">
							<Button
								variant="subtle"
								disabled={isSubmitting}
								onClick={() => {
									setSelectedSport(null)
									handleModal.close()
								}}
								color="red"
							>
								Cancel
							</Button>
							<Button
								type="submit"
								loading={isSubmitting}
								loaderPosition="right"
							>
								{mode === MODE.edit ? "Save changes" : "Add sport"}
							</Button>
						</div>
					</fieldset>
				</fetcher.Form>
			</Modal>
		</>
	)
}
